//     display: flex;
// }

.cookie-popup-actions button {
    color: orange;
    border: none;
    background: none;
    font-family: inherit;
    font-style: inherit;
    font-size: inherit;
    font-weight: bold;
    text-transform: uppercase;
    font-size: 	0.75rem;
    padding: 0;
    cursor: pointer;

    margin: 10px;
}

.cookie-popup-actions button:hover {
    text-decoration: underline;
}

@keyframes cookie-popup-in {
    from { bottom: -6.25rem; }
    to { bottom: 1.25rem; }
}
  