@import './colors.scss';
@import './responsive.scss';

.menu{
    display: flex;
    // un z-index puissant est nécessaire car la transition empêche les liens d'être cliquable
    // z-index: 1000;
}

.logo {
    margin-left: 1vw;

    @include media('<tablet'){
        margin-left: 2vw;
        margin-top: 1vw;
    }

    @include media('<phone'){
        margin-left: 4vw;
        margin-top: 3vw;
    }
}

.logo img {
    width: 38px;
    height: 38px;
    margin-top: 0.5vw;

    @include media('<phone'){
        margin-top: 3vw;
    }
} 

nav {
    display: flex;
    // width: 380px;
    // justify-content: space-between;
    font-size: 1.5vw;
    margin-left: auto;
    margin-right: 5%;
    margin-top: 1vw;

    z-index: 2;
    position: relative;

    @include media('<tablet'){
        margin-right: 8vw;
        margin-top: 2vw;

    }

    @include media('<phone'){
        margin-right: 17vw;
        margin-top: 5.5vw;
    }
}

.nav__back{
    @include media('<tablet'){
        margin-right: 10vw;
    }

    @include media('<phone'){
        margin-right: 21vw;
    }
}


.wrap {
    font-family: 'Anton', sans-serif;
    letter-spacing: 1.9px;
    color: whitesmoke;
    text-decoration: none;
    overflow-y: hidden;
    position: absolute;

    font-weight: bold;
    font-size: 1.2vw;
    
    // width: max-content;
    width: 100px;

    @include media('<tablet') {
        font-size: 2.5vw;
        width: 100px;
    }

    @include media('<phone'){
        font-size: 5vw;
        width: 100px;
    }
}


