@import '../../utils/reset.scss';

@import '../../utils/colors.scss';
@import '../../utils/responsive.scss';
@import '../../utils/navigation.scss';
@import '../../utils/transition.scss';

@import './button.scss';
@import './cookie.scss';


body {
    background-color: $background;
    color: $color-black;
    overflow: hidden;
    height: 100vh;
}


#smooth-wrapper{
    // max-width: 1200px;
    margin: 0 auto;
}

nav {
    margin-top: 0;
}

#nav {
    position: fixed;
    z-index: 2;
    mix-blend-mode: difference;
}

.menu-icon {
    justify-content: end;
    mix-blend-mode: difference;
}

// .menu-icon a {
//     position: fixed;
// }

.wrap {
    margin-top: 1vw;

    @include media('<tablet'){
        margin-top: 1.5vw;
    }

    @include media('<phone'){
        margin-top: 4vw;
    }
}

.desktop__version{
    @include media('<tablet'){
        display: none;
    }
    @include media('<phone'){
        display: none;
    }
}
.mobile__version {
    @include media('>desktop') {
        display: none;
    }
}

.section_1 {
    height: 100vh;
}

#title {
    height: 100vh;
}

.section__2 {
    @include media('<tablet'){
        margin-top: 100px;
    }

    @include media('<phone'){
        margin-top: 100px;
    }
}


.display__flex{
    display: flex;
    // width: 70vw;

    margin: 0 auto;
}
.img__horizontal{
    max-width: 580;
    max-height: 400px;
    object-fit: contain;
}
.img__vertical{
    max-width: 390;
    max-height: 700px;
    object-fit: contain;
    margin: 20px;
    
}
.bloc__img_1 {
    margin-top: -100px;
}
.bloc__img_1, .bloc__img_2 {
    text-align: left;
}

.main h1{
    font-size: 10vw;
}

.main h2{
    font-size: 2vw;
}

main p {
    font-size: 1vw;
}



  